import { createStore, applyMiddleware } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import thunk from './middlewares/filmsSearchMiddleware';
import localStorageSavingsMiddleware from './middlewares/localStorageSavingsMiddleware';
import setPagesMiddleware from './middlewares/setPagesMiddleware';
import combinedReducer from './combinedReducer';

const composeEnhancers = composeWithDevTools({});
const store = createStore(
  combinedReducer,
  composeEnhancers(
    applyMiddleware(thunk, localStorageSavingsMiddleware, setPagesMiddleware),
  ),
);
export default store;
