import actionTypes from '../actionTypes';
import { setActivePageActionCreator } from '../filmsSearch/filmsSearchActions';

const setPagesMiddleware = store => next => action => {
  if (action.type === actionTypes.SET_FILM_QUERY) {
    store.dispatch(setActivePageActionCreator(1));
    next(action);
  } else {
    next(action);
  }
};
export default setPagesMiddleware;
